
import Vue from 'vue'

export default Vue.extend({
  name: 'FileNameChooserDialog',
  data: () => {
    return {
      modal: false,
      nameInternal: '',
    }
  },
  props: {
    value: Boolean,
    name: {
      type: String,
      required: true,
    },
    existing: {
      type: String,
      required: false,
    },
    books: {
      type: Array,
      required: true,
    },
  },
  watch: {
    value(val) {
      this.modal = val
      if (val) {
        this.clear()
      }
    },
    existing: {
      handler(val) {
        this.nameInternal = val
      },
      immediate: true,
    },
    modal(val) {
      !val && this.dialogClose()
    },
  },
  methods: {
    clear() {
      this.nameInternal = this.name || this.existing || ''
    },
    change(val: string) {
      this.nameInternal = val
    },
    choose(){
      if(this.nameInternal) {
        this.$emit('update:name', this.nameInternal)
        this.dialogClose()
      }
    },
    dialogClose() {
      this.$emit('input', false)
    },
  },
})
